}

img {
  width: 48px;
  height: 48px;
}

.question-summary img,
.question-summary .summary {
  margin: 8px;
}

.timestamp {
  margin-bottom: 8px;
  color: gray;
}

.authed-user img {
  width: 32px;
  height: 32px;
  margin-top: -16px;
  margin-right: 10px;
}

.container {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px 40px 40px;
  background-color: whitesmoke;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
}

.authed-user, .top-bar {
  display: flex;
  flex-direction: row;
}

.authed-user {
  margin: auto 0;
  margin-top: 10px;
}

.top-bar {
  justify-content: space-between;
  padding: 0 8px;
  border-bottom: 2px solid #800000;
}

.sign-out {
  margin-left: 20px;
  cursor:pointer;
  background-color: darkred;
  color: white;
  border-radius: 4px;
  width: 80px;
  text-align: center;
  padding: 4px;
  margin-top: -10px;
  padding-top: 5px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
}

.nav ul {
  display: flex;
  flex-direction: row;
  text-decoration: none;
  margin: 0;
}

.nav li:first-child {
  padding-left: 0;
}

.active {
  color: darkred;
  font-weight: bold;
}

ul {
  padding-left: 0;
}

.nav li {
  padding: 10px;
}

li {
  list-style-type: none;
  text-decoration: none;
}

.question-summary {
  border: 1px solid darkred;
  border-radius: 4px;
  background-color: white;
  box-shadow: 0 2px 6px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
  display: flex;
  flex-direction: row;
  margin-top: 10px;
}

a {
  text-decoration: none;
  color: black;
}

.new-question {
  margin-top: 20px;
  width: 100%;
}

.title, .or {
  font-size: large;
  font-weight: bold;
  margin-top: 10px;
}

.new-question input {
  width: 400px;
  margin-top: 10px;
  height: 30px;
  font-size: large;
}

.submit {
  margin-top: 20px;
  width: 408px;
  height: 40px;
  background-color: darkred;
  color: white;
  border-radius: 6px;
  border: none;
  font-size: large;
  box-shadow: 0 3px 6px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
}

.question-page {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid darkred;
  border-radius: 4px;
  background-color: white;
  box-shadow: 0 2px 6px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
}

.question-page-summary {
  display: flex;
  flex-direction: row;
}

.question-page-summary img,
.question-page-summary .summary,
.unanswered {
  margin: 8px;
}

.option {
  margin-top: 10px;
}

.option input {
  height: 20px;
  margin-right: 10px;
}

.answer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 10px;
}

.answer-tag {
  color: darkred;
  font-weight: bold;
  margin-left: 5px;
}

.stats {
  text-align: right;
}

.leader-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 10px;
}

.fake-img {
  width: 48px;
}

.leader-row div {
  width: 100%;
  text-align: left;
  padding: 8px;
  border-bottom: 2px solid whitesmoke;
}

.leader {
  background-color: white;
}

.leader-header {
  height: 32px;
  padding-top: 16px;
  font-weight: bold;
}
